import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import PersonIcon from '@mui/icons-material/Person';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';

import {
  DateTimeField,
  Loader,
  SearchField,
  SearchMultiField,
  SearchLocationField,
  SelectMultiField,
  TextField,
} from '../components';

import { LoadUpdate, LoadQuery, UsersQuery, TrucksQuery } from '../api';
import { useSnackbar } from '../hooks';
import { UpdateLoad, TruckTypes, User, Truck } from '../utils/dataTypes';
import { cleanObject } from '../utils/queryHelpers';

const validationSchema = yup.object({
  pick: yup
    .object({
      formatted_address: yup.string().required(),
      geometry: yup
        .object({
          location: yup
            .object({
              lat: yup.mixed().required(),
              lng: yup.mixed().required(),
            })
            .required(),
        })
        .required(),
    })
    .required('Pick location is required'),
  pickDate: yup
    .date()
    .typeError('Enter correct date')
    .required('Pick Date is required'),
  deliver: yup
    .object({
      formatted_address: yup.string().required(),
      geometry: yup
        .object({
          location: yup
            .object({
              lat: yup.mixed().required(),
              lng: yup.mixed().required(),
            })
            .required(),
        })
        .required(),
    })
    .required('Deliver location is required'),
  deliverDate: yup
    .date()
    .typeError('Enter correct date')
    .required('Deliver Date is required'),
  weight: yup.string().required('Weight is required'),
  truckType: yup
    .array(yup.string().oneOf(TruckTypes))
    .min(1, 'Select at list one')
    .required('Truck Type is required'),
  rate: yup.number().typeError('Rate must be a number'),
  bookedByUser: yup
    .object({ id: yup.string().required() })
    .typeError('Enter correct user')
    .nullable(),
  bookedByCompany: yup.string(),
  dispatchers: yup.array(
    yup.object({ id: yup.string().required() }).typeError('Enter correct user'),
  ),
  checkInAs: yup.string(),
  truck: yup
    .object({ id: yup.string().required() })
    .typeError('Enter correct truck')
    .nullable(),
});

const initialValues = {
  id: '',
  pick: null,
  pickDate: undefined,
  deliver: null,
  deliverDate: undefined,
  weight: undefined,
  truckType: [],
  rate: undefined,
  bookedByUser: null,
  bookedByCompany: undefined,
  dispatchers: [],
  checkInAs: '4ULogistics',
  truck: null,
};

export default function EditLoadForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['user', pathname.split('/')[3]],
    queryFn: LoadQuery,
    keepPreviousData: true,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const { mutate, isLoading } = useMutation(LoadUpdate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Saved',
      });
      queryClient.invalidateQueries(['loads']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<UpdateLoad>({
    initialValues: {
      ...initialValues,
      ...cleanObject(data, Object.keys(initialValues)),
    },
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values as unknown as UpdateLoad);
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <SearchLocationField
                fieldName="pick"
                label="Pick Location"
                formik={formik}
              />
              <DateTimeField
                fieldName="pickDate"
                label="Pick Date"
                formik={formik}
              />
              <TextField fieldName="weight" label="Weight" formik={formik} />
              <SelectMultiField
                fieldName="truckType"
                label="Truck Type"
                items={TruckTypes}
                formik={formik}
              />
              <SearchField<User>
                fieldName="bookedByUser"
                label="Booked By"
                getSelectedLabel={(selectedItem) => `${selectedItem.fullName}`}
                getItemLabel={(item) => `${item.fullName}`}
                getItemSecondLabel={(item) => `${item.jobTitle}`}
                ListIcon={PersonIcon}
                queryFn={UsersQuery}
                orderby="fullName"
                order="asc"
                formik={formik}
              />
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%' }}>
              <SearchLocationField
                fieldName="deliver"
                label="Deliver Location"
                formik={formik}
              />
              <DateTimeField
                fieldName="deliverDate"
                label="Deliver Date"
                formik={formik}
              />
              <TextField fieldName="rate" label="Rate" number formik={formik} />
              <TextField
                fieldName="checkInAs"
                label="Check In As"
                formik={formik}
              />
              <TextField
                fieldName="bookedByCompany"
                label="Booked By Company"
                formik={formik}
              />
            </Box>
          </Box>
          <Box
            key="bottomControls"
            sx={{ marginTop: -2, marginBottom: 3, p: 1 }}
          >
            <SearchField<Truck>
              fieldName="truck"
              label="Truck"
              getSelectedLabel={(selectedItem) => `${selectedItem.truckNumber}`}
              getItemLabel={(item) => `${item.truckNumber}`}
              getItemSecondLabel={(item) =>
                `${item.status}, ${item.licencePlate}`
              }
              ListIcon={LocalShippingIcon}
              queryFn={TrucksQuery}
              orderby="truckNumber"
              order="asc"
              formik={formik}
            />
            <SearchMultiField<User>
              fieldName="dispatchers"
              label="Dispatchers"
              getSelectedLabel={(selectedItem) => `${selectedItem.fullName}`}
              getItemLabel={(item) => `${item.fullName}`}
              getItemSecondLabel={(item) => `${item.jobTitle}`}
              ListIcon={PersonIcon}
              queryFn={UsersQuery}
              orderby="fullName"
              order="asc"
              formik={formik}
            />
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
                disabled={!formik.dirty}
              >
                Save
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading || loading || fetching} />
    </Box>
  );
}
